.recycled_section h1{
    padding: 2rem 0 0 0;
    font-size: 2rem;
}
.recycled_boxes{
    width: 30rem;
    height: auto;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 25px 25px #00000033;
opacity: 1;
margin: 1rem 0 2rem 0;
}
.blue_bg{
    width: 30rem;
    height: 3rem;
    background: #59B2D6 0% 0% no-repeat padding-box;
opacity: 1;
}
.red_bg{
    width: 30rem;
    height: 3rem;
    background: #DE4A4A 0% 0% no-repeat padding-box;
    opacity: 1;
}
.blue_bg h1, .red_bg h1{
   font-size: 1.2rem;
   color: #fff;
   padding: 1rem;
}
.trangale_dark2{
    display: inline-block;
    width: 1rem ;
    height: 1rem ;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    color: #2D4453;
    background: #2D4453 0% 0% no-repeat padding-box ;
opacity: 1;
margin: 1rem 0 0 .5rem;
}
.trangale_dark2 p{
    display: inline-block;
    padding: .5rem 0 0 .5rem;
    color: #2D4453 !important;

}
.recycled_headings p{
    display: inline-block;
    padding: .5rem 0 0 .5rem;
    color: #2D4453;
}
@media screen and (max-width:800px){
    .recycled_boxes{
        width: 100%;
        
    }
    .blue_bg{
        width: 100%;
        /* height: 2rem; */
       
    }
    .red_bg{
        width: 100%;
        
    }
    .blue_bg h1, .red_bg h1{
        font-size: 1rem;
        padding: .5rem;
        
     }
}