
/* services boxes css here */
.all_services_box11 {
   position: relative;
   width: 90%;
   height: 18rem;
   margin-bottom: 1rem;
   background: #ffffff 0% 0% no-repeat padding-box;
   box-shadow: 0px 35px 35px #0000001c;
   opacity: 1;
   padding: 0 .5rem;
   margin-top: 2rem;
 }
 .all_services_box11:hover {
 border: 2px solid orange;
 }
 .ALL_SERVICES_imgs {
   position: absolute;
   width: 3rem;
   height: 3rem;
   top: 30%;
   left: 50%;
   transform: translate(-50%, -50%);
 }
 .all_services_box11 h3 {
   padding-top: 8rem;
   font-size: 1.5rem;
   text-align: center;
   color: #18a8e2;
   text-transform: uppercase;
   opacity: 1;
   letter-spacing: 1px;
   
 }
 .all_services_box11 p {
   text-align: center;
   padding: 0.5rem;
   font-size: 0.8rem;
   color: #333;
 }
.navlink_div{
   text-decoration: none !important;
   cursor: pointer;
}
.recyling_headings h1{
    padding: 3rem 0 1rem 0;
    color: #2D4453;
    font-size: 2.5rem;
}
.recyling_headings img{
   width: 100%;
   height: 25rem;
   object-fit: cover;
   
}
.recyling_headings h5{
    padding: 1rem 0 0 0;
    color: #2D4453;
    font-size: 1.2rem;
    line-height: 2;
 }
 .text_blue{
    color: #18A8E2;
 }
 .myrowclass{
     display: flex;
     flex-direction: row;
     margin: .2rem;
 }
 .vl {
    border-left: 6px solid green;
    height: 40px;
    margin: .2rem;
  }
  .v2 {
    border-left: 6px solid green;
    height: 90px;
    margin: 2rem .2rem 0 .2rem;
  }
  .v3 {
    border-left: 6px solid green;
    height: 50px;
    margin: .2rem;
  }


 /* all subservices css start here */
 .services_banners {
    position: relative;
 }
 .services_banners img{ 
    height: 75vh;
    width: 100%;}
 .services_banners h1{
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
   text-align: center;
   font-size: 4rem;
   color: #fff;
}
.subbottom_pera p{
   padding: 1.5rem 0 0 0;
   line-height: 2;
}
.second_bottom{
   background: lightblue;
   padding: 2rem 0;
}
.i7_help_pera h2{
   font-size: 2.5rem;
}
.i7_help_pera p{
line-height: 2;
}
.third_bottom{
   margin-top: 2rem;
}
.third_bottom h2{
  font-size: 3rem;
}
.third_bottom p{
line-height: 1.8;
}
.img_circle{
   width: 25rem;
   height: 15rem;
}
.img_circle2{
   width: 100%;
   height: 100%;
}
.img_logistics{
   width: 100%; 
   height:30rem !important;
  
}
