.MAIN_SLIDER{
    margin: 5rem 0 5rem 0;
}
.all_services_box2{
    position: relative;
    width: 90%;
    height: 17rem;
    margin-bottom: 1rem;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 35px 35px #0000001C;
opacity: 1;
}
.all_services_box3{
    position: relative;
    width: 100%;
    height: 18rem;
    margin-bottom: 0rem;
    margin-top: 1rem;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 35px #0000001C;
opacity: 1;
}
.ALL_SERVICES_imgs{
    position: absolute;
    width: 3rem;
    height: 3rem;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.ALL_SERVICES_imgs2{
    position: absolute;
    width: 5rem;
    height: 5rem;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.all_services_box2 h3{
    padding-top: 8rem;
    font-size: 1rem;
    text-align: center;
    color: #18A8E2;
    text-transform: uppercase;
    opacity: 1;
}
.all_services_box2 p{
    text-align: center;
    padding: .5rem;
    font-size: .8rem;
  
}
.all_services_box3 h3{
    padding-top: 8rem;
    font-size: 2rem;
    text-align: center;
    color: #18A8E2;
    text-transform: uppercase;
    opacity: 1;
}
.all_services_box3 p{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: .5rem;
    font-size: .8rem;
    width: 50%;
  
}
.slick-arrow{
    color: black !important;
}
.slick-prev{
    z-index: 100;
    left: 20px !important; 
}
.slick-next{
    z-index: 100;
    right: 40px !important;
   
}
.slick-prev:before, .slick-next:before {
    font-family: 'slick';
    font-size: 40px !important;
    line-height: 1;
    opacity: .75;
    color: #4AE870 !important;
  
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
@media screen and (max-width:800px){
    .all_services_box2{
        position: relative;
        width:95%;
        height: 17rem;
    }
    .all_services_box3{
        position: relative;
        width:100%;
        height: 17rem;
 
    }
    .ALL_SERVICES_imgs2{
        width: 2.5rem;
        height: 2.5rem;
        top: 15%;
    }
    .all_services_box3 h3{
        padding-top: 5rem;
        font-size: 1rem;
    }
    .all_services_box3 p{
        position: absolute;
        top: 60%;
        left: 50%;
        padding: 0 4rem;
        font-size: .8rem;
        width: 100%;
    }
}