.store_images{
    margin: 3rem 0;
}
.store_section h1{
    font-size: 2rem;
    padding-top: 3rem;
}
.store_section h3{
    font-size: 1.3rem;
    line-height: 2;
}
.request_box{
    margin: 2rem 0;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
}
.request_box p{
    line-height: 2;
    padding: .5rem;
}
@media screen and (max-width:800px){
    .store_images{
        margin: 1rem 0;
    }
    .store_images img{ 
        margin: .5rem 0;
        width: 100%;
    }
    .store_section h1{
        font-size: 1.5rem;
        padding-top: 1rem;
    }
    .store_section h3{
        font-size: 1rem;
        line-height: 2;
    }
    .request_box{
       
        margin: 2rem 1rem;
       
    }
    .request_box p{
        font-size: .8rem;
        line-height: 2;
        padding: .5rem;
    }
}