.products_boxes{
    margin: 2rem 0;
width: 100%;
height: 18rem;
background: #70CCF1 0% 0% no-repeat padding-box;
opacity: 1;
}
.products_boxes h4{
    text-align: center;
    padding: 1rem 0 0 0;
}
.products_boxes h5{
    text-align: center;
    padding: 1rem 0 0 0;
    line-height: 2;
}
@media screen and (max-width:800px){
    .products_boxes{
        margin: 1rem 0;
    width: 100%;
    height: 12rem;
    background: #70CCF1 0% 0% no-repeat padding-box;
    opacity: 1;
    }
    .products_boxes h4{
        font-size: 1.2rem;
    }
    .products_boxes h5{
        line-height: 1.5;
        font-size: .9rem;
    } 
}